$breakpoint-mobile: 600px;
$breakpoint-tablet: 820px;
$breakpoint-laptop: 1368px;

@mixin if-mobile {
  @media screen and (min-width: 5rem) and (max-width: $breakpoint-mobile) {
    @content;
  }
}

@mixin if-tablet-mini {
  @media screen and (max-width: $breakpoint-tablet) {
    @content;
  }
}
